import React from 'react'
import Carditem from './Carditem'

function Cards() {
    return (
        <div className='cards'>
            <h1 className="">Check out these epic destination</h1>
            <div className="cards__container">
                <div className="cards__wrapper">
                    <ul className="cards__items">
                        <Carditem
                        src='images/img-9.jpg'
                        text='Explore the hidden waterfall' 
                        label='Adventure'
                        path='/services' />
                        <Carditem
                        src='images/img-1.jpg'
                        text='Explore the beauty of Taj Mahal' 
                        label='Adventure'
                        path='/services' />
                    </ul>
                    <ul className="cards__items">
                        <Carditem
                        src='images/img-2.jpg'
                        text='Explore Lotus Temple' 
                        label='Adventure'
                        path='/services' />
                        <Carditem
                        src='images/img-3.jpg'
                        text='Explore Mysore palace' 
                        label='Adventure'
                        path='/services' />
                        <Carditem
                        src='images/img-4.jpg'
                        text='Explore Golden Temple' 
                        label='Adventure'
                        path='/services' />
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Cards
