
import React,{useState,useEffect} from 'react'
import {Link} from 'react-router-dom';
import Button from '../Button/Button';
import '../Navbar/Navbar.css';

function Navbar() {
    const [click,setClick]=useState(false);
    const [button,setButton]=useState(true);

    const showButton = () =>{
        if(window.innerWidth <=960){
            setButton(false);
        }else{
            setButton(true);
        }
    }

    useEffect(()=>{
        showButton();
    },[]);

    window.addEventListener('resize',showButton);

    const handleclick=()=>{
        setClick(!click);
    }

    const closemobile=()=>{
        setClick(false);
    }

    return (
        <>
        <nav className="navbar">
            <div className="navbar__container">
                <Link to='/' className='navbar--logo' onClick={closemobile}>Logo</Link>
                <div className="menu__icon" onClick={handleclick}>
                    <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
                </div>
                <ul className={click ? 'nav__menu active' : 'nav__menu'}>
                    <li className='nav__item'>
                    <Link to='/' className='nav__links' onClick={closemobile}>Home</Link>
                    </li>
                    <li className='nav__item'>
                    <Link to='/services' className='nav__links' onClick={closemobile}>Service</Link>
                    </li>
                    <li className='nav__item'>
                    <Link to='/products' className='nav__links' onClick={closemobile}>Products</Link>
                    </li>
                </ul>
                {button && <Button buttonstyle='btn--outline'>Sign UP</Button>}
            </div>
        </nav>
        </>
    )
}

export default Navbar
